.intro {
  background-color: @white;
}

.intro__container {
  min-height: 389px;
  background-color: @blue;
  background-image: url("../img/intro/back.jpg");
  background-repeat: no-repeat;
}

.intro__container--inner {
  min-height: 158px;
  background-size: cover;
}

.intro__description {
  padding: 37px 20px 25px 20px;
  text-align: center;
  background-color: @white;
}

.intro__title {
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: @black;
}

.intro__text {
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  color: @darkgray;
}

.intro__image {
  display: block;
  position: relative;
  right: 50%;
  transform: translateX(90%);
  margin-bottom: 52px;
}
